import request from '@/utils/request'
import { ouchnCustom } from "@/service"

// 在线阅读epub——添加笔记
export function insertNote(data) {
  return request({
    url: ouchnCustom + '/web/api/note/addNote',
    method: 'post',
    data: data,
    header: {
      token: true
    }
  })
}
// 在线阅读epub——修改笔记
export function update(data) {
  return request({
    url: ouchnCustom + '/web/api/note/updateNote',
    method: 'post',
    data: data,
    header: {
      token: true
    }
  })
}
// 在线阅读epub——删除笔记
export function removeNoteById(noteId) {
  return request({
    url: ouchnCustom + '/web/api/note/deleteNote',
    method: 'post',
    params: { noteId },
    header: {
      token: true
    }
  })
}
// 根据资源文件id查询所有笔记
export function selectNoteListByFileId(filesId) {
  return request({
    url: ouchnCustom + '/web/api/note/getNoteInfo',
    method: 'get',
    params: { filesId },
    header: {
      token: true
    }
  })
}
