// 获取扫码资源信息
export function GetSource(codeId) {
  return request({
    url: ouchnCustom + '/getSource?codeId=' + codeId,
    method: 'get',
  })
}

// 扫描在线扫码项目二维码后进入的跳转接口
export function transOnlineProjectCode(id, isApp) {
  return request({
    url: ouchnCustom + '/transOnlineProjectCode/' + id + isApp,
    method: 'get',
  })
}

// 获取在线扫码项目下的资源
export function getOnlineCodeResource(onlineCodeId) {
  return request({
    url: ouchnCustom + '/getOnlineCodeResource/' + onlineCodeId,
    method: 'get',
  })
}

// 通过在线扫码项目二维码进入的资源添加扫码记录
export function onlineResourceId(onlineResourceId, remoteAddr, userAgent, userName) {
  return request({
    url: ouchnCustom + '/onlineResourceId/' + onlineResourceId + '?remoteAddr=' + remoteAddr + '&userAgent=' + userAgent + '&userName=' + userName,
    method: 'get',
  })
}
